import { useEffect, useState } from "react";
import { nowPlaying, popular, topRated, upcoming } from "../../api";
import { Loading } from "../../components/Loading";
import { Movies } from "./components/Movies";
import { MainBanner } from "./components/MainBanner";

export const Home = () => {
  const [nowData, setNowData] = useState();
  const [popData, setPopData] = useState();
  const [topData, setTopData] = useState();
  const [upData, setUpData] = useState();
  const [isloading, setIsLoading] = useState(true);

  useEffect(() => {
    (async () => {
      try {
        const { results: nowResults } = await nowPlaying();
        const { results: popResults } = await popular();
        const { results: topResults } = await topRated();
        const { results: upResults } = await upcoming();
        // =>       (:)     
        // console.log(nowResults);

        setNowData(nowResults);
        setPopData(popResults);
        setTopData(topResults);
        setUpData(upResults);
        setIsLoading(false);
      } catch (error) {
        console.log(error);
      }
    })();
  }, []);

  console.log(nowData);
  // console.log(popData);
  // console.log(topData);
  // console.log(upData);
  console.log(isloading);

  // console.log(isloading);

  return (
    <>
      {isloading ? (
        <Loading />
      ) : (
        <>
          <MainBanner data={nowData[0]} />

          <Movies movieData={nowData} />
          <Movies title="  " movieData={nowData} />
          <Movies title=" " movieData={popData} />
          <Movies title=" " movieData={topData} />
          <Movies title=" " movieData={upData} />
        </>
      )}
    </>
  );
};
